#top-jumbo {
    padding: 0 0 400px;
    background: #ffffff; /* Old browsers */
    background: -moz-linear-gradient(-45deg, #ffffff 0%, #ffffff 40%, #8948f2 77%, #e0a1df 100%); /* FF3.6-15 */
    background: -webkit-linear-gradient(-45deg, #ffffff 0%, #ffffff 40%, #8948f2 77%,#e0a1df 100%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(135deg, #ffffff 0%, #ffffff 40%, #8948f2 77%,#e0a1df 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
}
#top-jumbo:after{
    content: "";
    display: table;
    clear: both;
}
#top-jumbo .left-side{
    width: 40%;
    display: inline-block;
    float: left;
}
#top-jumbo .left-side .large-number{
    font-size: 300px;
    font-weight: 700;
    opacity: .1;
    color: #4C44C1;
    position: relative;
    left: -20px;
    top: 0px;
    z-index: 1;
}
#top-jumbo .left-side .info-container{
    position: relative;
    top: -100px;
}
#top-jumbo .left-side .info-container h1{
    color: #6066F2;
    font-size: 4.5rem;
    text-transform: capitalize;
    margin-bottom: 1.5rem;
    line-height: 1.1;
}
#top-jumbo .left-side .info-container p{
    color: #aaacaf;
    font-size: 1.5rem;
    line-height: 1.8;
    font-weight: 300;
    margin-bottom: 1.5rem;
}
#top-jumbo .left-side .info-container .email-area{
    background: white;
    border-radius: 50px;
    display: inline-block;
    padding: 10px;
}
#top-jumbo .left-side .info-container .email-area svg {
        position: relative;
        font-size: 2rem;
        top: 7px;
        margin-left: 15px;
        color: #d5e2ea;
    }
#top-jumbo .left-side .info-container .email-area input[type="email"] {
        background: white;
        padding: 10px;
        border: none;
        text-transform: uppercase;
        font-size: 1rem;
        width: 230px;
    }
#top-jumbo .left-side .info-container .email-area button {
        background: #6066F2;
        padding: 20px 50px;
        color: white;
        text-transform: uppercase;
        border-radius: 50px;
        font-size: 1rem;
}
#top-jumbo .right-side{
    width: 60%;
    display: inline-block;
    float: left;
    padding: 0 40px;
}
#top-jumbo .right-side img{
    width: 100%;
}
/*Laptop Devices*/
@media only screen and (max-width: 1215px) {
    #top-jumbo .left-side .info-container .email-area {
        width: 100%;
    }

    #top-jumbo .left-side .info-container h1 {
        font-size: 3.5rem;
    }

    #top-jumbo .left-side .info-container .email-area svg {
        position: relative;
        top: 14px;
        float: left;
        width: 15%;
        text-align: center;
        margin-left: 0;
    }

    #top-jumbo .left-side .info-container .email-area input[type="email"] {
        width: 50%;
        float: left;
        position: relative;
        top: 12px;
    }

    #top-jumbo .left-side .info-container .email-area button {
        font-size: 1rem;
        float: left;
        background: #6066F2;
        padding: 20px 0;
        width: 35%;
    }
}
/*Tablet Devices*/
@media only screen and (max-width: 1023px) {
    #top-jumbo{
        padding: 0 0 300px;
    }
    #top-jumbo .left-side {
        width: 40%;
    }
    #top-jumbo .left-side .large-number{
        font-size: 250px;
    }
    #top-jumbo .left-side .info-container p{
        font-size: 1.2rem;
    }
    #top-jumbo .left-side .info-container .email-area{
        width: 100%;
    }
    #top-jumbo .left-side .info-container h1{
        font-size: 3.5rem;
    }
    #top-jumbo .left-side .info-container .email-area svg{
        position: relative;
        top: 14px;
        float: left;
        width: 15%;
        text-align: center;
        margin-left: 0;
    }
    #top-jumbo .left-side .info-container .email-area input[type="email"]{
        width: 50%;
        float: left;
        position: relative;
        top: 12px;
    }
    #top-jumbo .left-side .info-container .email-area button{
        font-size: 1rem;
        float: left;
        background: #6066F2;
        padding: 20px 0;
        width: 35%;
    }
    #top-jumbo .right-side {
        width: 60%;
    }
    #top-jumbo .right-side img{
        top: 156px;
        position: relative;
    }
}
/*Mobile devices*/
@media only screen and (max-width: 767px) {
    #top-jumbo{
        padding: 0 0 300px;
    }
    #top-jumbo .left-side {
        width: 100%;
    }
    #top-jumbo .left-side .large-number{
        font-size: 250px;
    }
    #top-jumbo .left-side .info-container p{
        font-size: 1.2rem;
    }
    #top-jumbo .left-side .info-container .email-area{
        width: 100%;
    }
    #top-jumbo .left-side .info-container h1{
        font-size: 3.5rem;
    }
    #top-jumbo .left-side .info-container .email-area svg{
        position: relative;
        top: 14px;
        float: left;
        width: 15%;
        text-align: center;
        margin-left: 0;
    }
    #top-jumbo .left-side .info-container .email-area input[type="email"]{
        width: 50%;
        float: left;
        position: relative;
        top: 12px;
    }
    #top-jumbo .left-side .info-container .email-area button{
        font-size: 1rem;
        float: left;
        background: #6066F2;
        padding: 20px 0;
        width: 35%;
    }
    #top-jumbo .right-side {
        width: 100%;
    }
    #top-jumbo .right-side img{
        top: 0;
    }
}