/*
    #FEA83B orange
    #6066F2 light blue
    #B286F8 purple
    #4C44C1 blue
    #E6CDF9 light purple
*/
.container{
    max-width: 1400px;
    margin: 0 auto;
    padding: 0 20px;
}
.container-fluid {
    margin: 0 5%;
    padding: 0 20px;
}
.box-shadow-down {
    -webkit-box-shadow: 0px 17px 40px -10px rgba(0,0,0,0.3);
    -moz-box-shadow: 0px 17px 40px -10px rgba(0,0,0,0.3);
    box-shadow: 0px 17px 40px -10px rgba(0,0,0,0.3);
}
.box-shadow-center {
    -webkit-box-shadow: 0px 0px 65px 13px rgba(158,158,158,0.2);
    -moz-box-shadow: 0px 0px 65px 13px rgba(158,158,158,0.2);
    box-shadow: 0px 0px 65px 13px rgba(158,158,158,0.2);
}
input::placeholder{
    color: #d5e2ea;
}
